@use 'base/mixins' as mix;
@use 'base/variables' as var;


.galeria{
    @include mix.telefono{
        padding: 5rem 0rem;
    }
    @include mix.tablet{
        padding: 5rem 0rem;
    }
    @include mix.desktop{
        padding: 10rem 5rem;
    }
    h3{
        text-align: center;
    }
}

.galeria-imagenes{
    @include mix.resetear-listas; 
    @include mix.grid(2, 2rem); 

     @include mix.telefono{
        @include mix.grid(3, 2rem); 
    }
    @include mix.tablet{
        @include mix.grid(4, 2rem); 
    }
    @include mix.desktop{
        @include mix.grid(6, 2rem); 
    }
}
.ovarlay{
    position: fixed;
    top: 0;
    left: 0; 
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100;
    display: grid;
    place-items: center;
    background-color: rgba(0, 0, 0, .7);
    picture{
       display: block;
       max-width: 90rem; 
    }
}

.btn-cerrar{
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;

    background-color: var.$blanco;
    color: var.$negro; 
    display: grid;
    place-items: center;
    font-weight: 900;
    &:hover{
    cursor: pointer;
    }
}