@mixin telefono {
    @media (min-width: var.$telefono) {
        @content; 
    }
}
@mixin tablet {
    @media (min-width: var.$tablet) {
        @content; 
    }
}
@mixin desktop {
    @media (min-width: var.$desktop) {
        @content; 
    }
}
@mixin desktop {
    @media (min-width: var.$desktopxl) {
        @content; 
    }
}

@mixin grid($columnas, $separacion) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $separacion;
}

@mixin resetear-listas{
    margin: 0;
    padding: 0;
    list-style: none;
}