@use 'base/mixins' as mix;
@use 'base/variables' as var;

.sobre-festival{
    padding: 5rem 0;
    @include mix.tablet{
      @include mix.grid(2, 5rem);
    }
}
.contenido-festival{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
     @include mix.tablet{
      margin-top: 0;
    }
    h2{
     margin-bottom: 2rem;
    }
    .fecha{
     color: var.$rosa;
     font-weight: bold;
    }
}