@use 'base/variables' as var;
@use 'base/mixins' as mix;


.lineup{
    background-color: var.$rosa;
    padding: 5rem 0;

    h3, p{
        text-align: center;
        color: var.$blanco;
    }
    .dia{
        text-transform: uppercase;
        margin: 5rem 0;
    }
}
.ecenarios-contenedor{
     @include mix.tablet{
         @include mix.grid(2, 5rem);        
    }
    p{
        color: var.$negro;
    }
    .ecenario{
        padding-top: 3rem;
    }
    p{
        &:after{
         content: '';
         display: block;
         width: 20rem;
         height: 10rem;
         background-size: 15rem;
         background-repeat: no-repeat;
         background-position: center center;
         margin: 2rem auto 0 auto;   
        }
    }
}

.edm .nombre-ecenario:after{
    background-image: url(../img/icono_edm.png);
}
.rock .nombre-ecenario:after{
    background-image: url(../img/icono_rock.png);
}
.calendario{
    @include mix.resetear-listas; 
    li{
        padding: 2.5rem;
        font-weight: 700;
        span{
           font-weight: 400;
        }
    }
}
.bg-amarillo{
    background-color: var.$amarillo;

    li:nth-child(odd) {
      background-color: darken(var.$amarillo, 20%);
    }
}

.bg-verde{
    background-color: var.$verde;

    li:nth-child(odd) {
        background-color: darken(var.$verde, 12%);
      }
}