
@use 'base/variables' as var;
@use 'base/mixins' as mix;

.video{
position: relative;
max-height: 80rem;
min-height: 50rem;
overflow: hidden;
.overlay{
    position: absolute;
    height: 100%;
    width: 100%;
    background: rgb(191,53,221);
    background: linear-gradient(90deg, rgba(191,53,221,0.6186437099253764) 0%, rgba(219,0,255,0.17886779956123078) 100%);
    display: flex;
    flex-direction: column;
    justify-content: center;
}
 video{
     width: auto;
     display: block;
      @include mix.desktop{
        width: 100%;
     }
  }
}

.contenido-video{
  z-index: 1;
    h2{
      color: var.$blanco;
      font-size: 7rem;
      margin: 0;
    }
    p{
       background-color: var.$rosa;
       display: inline-block;
       padding: 1rem 2rem;
       color: var.$blanco;
       font-weight: bold;
       font-size: 2.6rem;
       margin: 0;
    }
}