@use 'base/variables' as var;
@use 'base/mixins' as mix;    

.boletos{
    padding: 5rem 0;
    h3{
    text-align: center;
    color: var.$blanco;
    margin-bottom: 2rem;
    }
    .contenedor-boletos{
         @include mix.tablet{
            @include mix.grid(2, 5rem); 
        }
    }
}

.pase{
    color: var.$negro;
    .nombre-pase{
        text-align: center;
        font-size: 3rem;
        color: var.$blanco;
    }
    &-header,
    &-footer{
        padding: 0.5rem;
        p{
            text-align: center;
            color: var.$blanco;
            margin: 0;
        }
    }
   &-header{
        background-color: var.$rosa;
        P{
            text-transform: uppercase;
            font-size: 1.8rem;
        }
   }

   &-body{
    background-color: var.$blanco;
     ul{
        @include mix.resetear-listas;
        li,
        :nth-child(odd){
          background-color: var.$blanco;
          padding: 1rem;
          border-bottom: 1px solid #e1e1e1;
        }
     }
   }
   &-footer{
    background-color: var.$morado;
    p{
        font-size: 2.4rem;
        font-weight: 700;
    }
   }


}